[tooltip]:not([flow])::after, [tooltip][flow^="up"]::after { bottom: calc(100% + 5px); }

[tooltip]:not([flow])::before, [tooltip]:not([flow])::after { left: 50%; transform: translate(-50%, -0.5em); }

[tooltip][flow^="up"]::before, [tooltip][flow^="up"]::after { left: 50%; transform: translate(-50%, -0.5em); }

[tooltip][flow^="down"]::before { top: 100%; border-top-width: 0; border-bottom-color: #333; }

[tooltip][flow^="down"]::after { top: calc(100% + 5px); }

